@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100&family=Poppins:ital,wght@0,100;1,200&family=Tilt+Prism&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .bannerIcon {
    @apply w-16 h-16 bg-black bg-opacity-25 text-gray-200 text-xl 
    inline-flex items-center justify-center rounded-md shadow-shadowOne 
    hover:bg-opacity-40 hover:-translate-y-1 transition-all hover:text-designColor
    cursor-pointer duration-300;
  }
  .resumeLi {
    @apply w-full h-20 bg-black bg-opacity-25 text-xl text-gray-300 flex justify-center cursor-pointer 
    hover:bg-opacity-40 duration-300 items-center border-[1px] rounded-none;
  }
  .gbOpacity {
    @apply bg-black bg-opacity-40;
  }
  .contactInput {
    @apply w-full h-12 rounded-lg border-b-[1px] border-b-gray-600 bg-[#191b1e]
    text-lightcolor px-4 active:outline-none focus-visible:outline-designColor 
    outline-none focus-visible:border-b-transparent duration-300;
  }
  .contactTextArea {
    @apply w-full rounded-lg border-b-[1px] border-b-gray-600 bg-[#191b1e]
    px-4 py-2 active:outline-none focus-visible:outline-designColor outline-none 
    focus-visible:border-b-transparent duration-300 resize-none;
  }
}
.active {
  color: white;
}
.image1 {
  border-radius: 50%;
}
#nav {
  text-align: justify;
}
#nav li {
  display: inline;
}
#nav a {
  text-decoration: none;
  padding: 200px; /* variable width */
}
